import AsyncValidator from 'async-validator'
import components from './components'
import mixin from './mixin'

export default {
  name: 'VuxFormField',
  inject: ['form'],
  components: {
    ...Object.values(Array.from(components))
  },
  mixins: [mixin],
  model: {
    prop: 'value',
    event: 'on-change'
  },
  props: {
    prop: String,
    value: [String, Number, Boolean, Array, Object, Date],
    title: String,
    placeholder: String,
    type: String,
    props: Object,
    events: Object,
    rules: [Object, Array],
    required: Boolean,
    message: String
  },
  computed: {
    fieldComponent() {
      const { component, type } = this
      if (component) {
        return component
      }
      return components[type]
    },
    isRequired() {
      const { rules, required } = this
      if (rules && rules.length) {
        const rulesRequired = rules.some(rule => rule.required)
        return rulesRequired || required
      }
      return required
    },
    modelValue() {
      return this.form.model[this.prop]
    }
  },
  watch: {
    fieldValue(val) {
      this.validate()
      this.$emit('on-change', val)
      this.form.model[this.prop] = val
    },
    modelValue(val) {
      this.fieldValue = val
    },
    value(val) {
      this.fieldValue = val

      // https://github.com/airyland/vux/blob/v2/src/components/x-textarea/index.vue#L120
      // 内容小于最大长度时需要手动更新文本框高度
      const { type } = this
      if (type === 'textarea' && !!val) {
        this.$nextTick(() => {
          this.$refs[`vux${type}`].updateAutosize()
        })
      }
    }
  },
  data() {
    return {
      fieldValue: this.value || this.form.model[this.prop],
      validateState: '',
      validateMessage: this.message,
      initialValue: ''
    }
  },
  methods: {
    getRules() {
      const selfRules = this.rules || []
      const requiredRule =
        this.isRequired !== undefined ? { required: !!this.isRequired } : []
      if (this.message && this.isRequired) {
        requiredRule.message = this.message
      }
      return selfRules.some(rules => rules.required)
        ? [].concat(selfRules)
        : [].concat(selfRules, requiredRule)
    },
    validate(cb) {
      const rules = this.getRules()
      const descriptor = {}
      descriptor[this.prop] = rules

      const validator = new AsyncValidator(descriptor)

      const model = {}
      model[this.prop] = this.fieldValue

      validator.validate(
        model,
        { firstFields: true },
        (errors, invalidFields) => {
          this.validateState = errors ? 'error' : 'success'
          this.validteMessage = errors ? errors[0].message : ''
          cb(this.validteMessage, invalidFields)
        }
      )
    },
    clearValidate() {
      this.validateState = ''
      this.validateMessage = ''
    },
    resetField() {
      this.clearValidate()
      this.fieldValue = this.initialValue
    }
  },
  created() {
    this.form.addField(this)
  },
  mounted() {
    this.initialValue = this.fieldValue
  },
  beforeDestroy() {
    this.form.removeField(this)
  }
}
